import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container, Image} from "react-bootstrap";
import me from "../Asset/me.webp";

function About() {
return (
    <div id="about" className="text-light bg-dark">
        <Container className="pt-5 pb-5">
            <h2 className="pt-4 pb-4 text-center">О себе</h2>
            <Image
                className="myimg"
                src={me}
                roundedCircle
                align="middle"
                height={250}
            />
            <p className="bio-text ml-4 d-inline-block align-middle">
                Привет, меня зовут Денис. <br/>
                Я увлекаюсь и занимаюсь программированием и отчасти сисадминством. <br/>
                Умею собирать ПК и писать простенькие сайты. <br/>
                Этот сайт, например, написан на ReactJS лично мной. <br/>
                Знаю Git, малость PHP на пару с JS и его библиотека ReactJS, конечно же HTML/CSS. <br/>
            </p>
        </Container>
    </div>
)
}
export default About;