import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Carousel, Image} from "react-bootstrap";
import nixsv from "../Asset/nixsv.webp";
import forest3 from "../Asset/forest2.webp";

function Examples() {
return (
    <div id="examples" className="text-light bg-dark">
        <div className="w-75 m-auto pt-5 pb-5">
            <h2 className="text-center pt-2 pb-2">Примеры работ</h2>
            <Carousel>
                <Carousel.Item>
                    <Image
                        className="d-block w-100"
                        src={nixsv}
                        alt="First slide"
                        rounded
                    />
                    <Carousel.Caption>
                        <h3><a href="https://wanetol72.github.io/nixsvru/">nixsv.ru</a></h3>
                        <p>Сайт игрового проекта</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <Image
                        className="d-block w-100"
                        src={forest3}
                        alt="Third slide"
                        rounded
                    />
                    <Carousel.Caption>
                        <h3>Third slide label</h3>
                        <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
    </div>
)
}
export default Examples;