import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Container} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faVk} from "@fortawesome/free-brands-svg-icons";
import {faAt, faHome} from "@fortawesome/free-solid-svg-icons";

function Contacts() {
return (
    <div id="contacts" className="text-light bg-dark ">
        <Container className="pt-5 pb-5">
            <h3 className="text-center pt-2 pb-2">Контакты</h3>
            <div style={{fontSize: '1rem'}}>
                <FontAwesomeIcon id="" className="hover align-middle" icon={faVk} fixedWidth size="lg" /> <p className="d-inline-block align-super mb-1"><a href="https://vk.com/panzerkampfwagen6tiger">Денис Купцов</a></p>
                <br/>
                <FontAwesomeIcon id="" className="hover align-middle" icon={faAt} fixedWidth size="lg" /> <p className="d-inline-block align-super mb-1">wanetols@gmail.com</p>
                <br/>
                <FontAwesomeIcon id="" className="hover align-middle" icon={faHome} fixedWidth size="lg" /> <p className="d-inline-block align-super mb-1">Г. Москва</p>
            </div>
        </Container>
    </div>
)
}
export default Contacts;